.contact-wrapper {
    margin: 100px 10%;
    border-radius: 20px;
    padding: 15px 30px 40px 30px;
}
.no-margin {
    margin: 0;
}

@media only screen and (min-width: 750px) {
    .contact-wrapper {
        margin: 100px 20%;
    }
    .contact-wrapper h2 {
        font-size: 2.2em;
    }
    .contact-wrapper h3 {
        font-size: 2em;
    }
    .contact-wrapper p {
        font-size: 1.8em;
    }
}

@media only screen and (min-width: 1024px) {
    .contact-wrapper p {
        font-size: 1em;
    }
}
